import axios from 'axios';
import { mapMutations } from 'vuex';

export default {
  name: 'LogoutPage',
  data() {
    return {
      error: false,
      spinner: true,
    };
  },
  mounted() {
    axios
      .post('/api/logout/')
      .then((response) => {
        if (response.data.success === true) {
          this.completeLogout();
        } else {
          this.error = true;
        }
      })
      .catch((error) => {
        if (error.response.status === 401) {
          this.completeLogout();
        } else {
          this.error = true;
          throw error;
        }
      });
  },
  methods: {
    ...mapMutations('profile', ['setLoggedIn', 'resetState']),
    completeLogout() {
      this.resetState();
      this.setLoggedIn(false);
      this.error = false;
      this.spinner = false;
      setTimeout(() => {
        this.$router.push({ name: 'login' });
      }, 2000);
    },
  },
};
